'use strict';
(function (global) {
    !('Meanbee' in global) && (Meanbee = {});
    !('ShippingRules' in Meanbee) && (Meanbee.ShippingRules = {});

    let register;

    Meanbee.ShippingRules.Register.Term = class extends Meanbee.ShippingRules.Register
    {
        constructor() {
            super();
        }

        add(key, child) {
            if (!this.has(key) && (new child) instanceof Meanbee.ShippingRules.Term) {
                this.children[key] = child;
            }
            return this;
        }

        static getSingleton() {
            if (!register) {
                register = new this;
            }
            return register;
        }
    };

})(window);