'use strict';
(function (global) {
    !('Meanbee' in global) && (Meanbee = {});
    !('ShippingRules' in Meanbee) && (Meanbee.ShippingRules = {});

    Meanbee.ShippingRules.Condition.Cart = class extends Meanbee.ShippingRules.Condition
    {
        constructor(index, parent) {
            super(index, parent);
        }

        static getCategory(context) { // eslint-disable-line no-unused-vars
            return 'Cart Conditions';
        }

        static getVariables(context) {
            let variables = {};
            if (!context) {
                variables['package_weight'] = { label: 'Total Weight', type: ['number'] };
                variables['package_quantity'] = { label: 'Total Items Quantity', type: ['number'] };
                variables['package_value'] = { label: 'Subtotal excl. Tax', type: ['number'] };
                variables['base_subtotal_incl_tax'] = { label: 'Subtotal incl. Tax', type: ['number'] };
                variables['package_value_with_discount'] = { label: 'Subtotal after Discount', type: ['number'] };
            }
            return variables;
        }
    }

    Meanbee.ShippingRules.Register.condition.add('Cart', Meanbee.ShippingRules.Condition.Cart);
})(window);