'use strict';
(function (global) {
    !('Meanbee' in global) && (Meanbee = {});
    !('ShippingRules' in Meanbee) && (Meanbee.ShippingRules = {});

    Meanbee.ShippingRules.Aggregator.Boolean = class extends Meanbee.ShippingRules.Aggregator
    {
        constructor(index, parent = null, container) {
            super(index, parent, container);
            this._combinator = Meanbee.ShippingRules.Aggregator.Boolean.CONJUNCTIVE;
            this.value = 1;
        }

        set combinator(param) {
            if (~[Meanbee.ShippingRules.Aggregator.Boolean.CONJUNCTIVE, Meanbee.ShippingRules.Aggregator.Boolean.DISJUNCTIVE].indexOf(param)) {
                this._combinator = param;
            }
            return this;
        }

        get combinator() {
            return this._combinator;
        }

        addChild(childClass, index, ...params) {
            index = (index === void 0 || index === null) ? this.children.length : index;
            let reindex = index !== this.children.length,
                child = new childClass(index, this, ...params);
            if (child instanceof Meanbee.ShippingRules.Condition || child instanceof this.constructor) {
                this.children.splice(index, 0, new childClass(index, this, ...params));
                if (reindex) this.reindexChildren();
                return this.children[index].id;
            } else {
                console.error(`ShippingRules: Boolean Aggregators only accept Conditions and Boolean Aggregators: ${childClass} passed.`);
            }
        }

        replaceChildByIndex(newChildClass, index, ...params) {
            this.removeChildByIndex(index);
            this.addChild(newChildClass, index, ...params);
            return this;
        }

        renderCombinator() {
            let me = this;
            return (<select id={`${me.id}-combinator`} onChange={event => me.combinator = event.target.value}>
                <option value={Meanbee.ShippingRules.Aggregator.Boolean.CONJUNCTIVE}>ALL</option>
                <option value={Meanbee.ShippingRules.Aggregator.Boolean.DISJUNCTIVE}>ANY</option>
            </select>);
        }

        renderValue() {
            let me = this;
            return (<select id={`${me.id}-value`} onChange={event => me.value = +event.target.value}>
                <option value="1">TRUE</option>
                <option value="0">FALSE</option>
            </select>);
        }

        renderChildSelector() {
            let me = this;
            return (<li id={`${me.id}.${me.children.length}`} onKeyDown={me.keyHandler.bind(me)} tabIndex={0}>
                <select id={`${me.id}-childselector`} aria-label="Condition" onChange={(event) => {
                    let child = (event.target.value === 'this') ? me.constructor : Meanbee.ShippingRules.Register.condition.get(event.target.value);
                    let id = me.addChild(child);
                    me.root.rerender();
                    me.root.focus(id);
                }}>
                    <option disabled="disabled" selected="selected">[SELECT]</option>
                    {Meanbee.ShippingRules.Register.condition.getAsOptions(me.context)}
                    <option value="this">Condition Combination</option>
                </select>
            </li>)
        }

        render() {
            let me = this;
            return (<li id={me.id} onKeyDown={me.keyHandler.bind(me)} tabIndex={0}>
                If {me.renderCombinator()} of these conditions are {me.renderValue()}: {me.renderRemoveButton()}
                {me.renderChildren()}
            </li>);
        }

        toJSON() {
            let obj = super.toJSON();
            obj.value = this.value;
            return obj;
        }
    }
    Object.defineProperties(Meanbee.ShippingRules.Aggregator.Boolean, {
        CONJUNCTIVE: {
            value: 'Conjunctive'
        },
        DISJUNCTIVE: {
            value: 'Disjunctive'
        }
    });

    Meanbee.ShippingRules.Register.aggregator.add('Boolean', Meanbee.ShippingRules.Aggregator.Boolean);
})(window);