'use strict';
(function (global) {
    !('Meanbee' in global) && (Meanbee = {});
    !('ShippingRules' in Meanbee) && (Meanbee.ShippingRules = {});

    Meanbee.ShippingRules.Term.Conditional = class extends Meanbee.ShippingRules.Term
    {
        constructor(index, parent) {
            super(index, parent);
            this.aggregator = new Meanbee.ShippingRules.Aggregator.Boolean(0, this);
        }

        renderValue() {
            let me = this;
            return (<input id={`${me.id}-value`} type="number" value={me.value} onKeyDown={event => me.value = event.target.value} />);
        }

        render() {
            let me = this;
            return (<li id={me.id} onKeyDown={me.keyHandler.bind(me)} tabIndex={0}>
                Constant value of {me.renderValue()}
                <span id={me.aggregator.id}>
                    if {me.aggregator.renderCombinator()} of these conditions are {me.aggregator.renderValue()}: {me.renderRemoveButton()}
                    {me.aggregator.renderChildren()}
                </span>
            </li>);
        }

        toJSON() {
            let obj = super.toJSON();
            obj.aggregator = this.aggregator;
            return obj;
        }

        static name() {
            return 'Conditional Value';
        }
    }

    Meanbee.ShippingRules.Register.term.add('Conditional', Meanbee.ShippingRules.Term.Conditional);
})(window);