'use strict';
(function (global) {
    !('Meanbee' in global) && (Meanbee = {});
    !('ShippingRules' in Meanbee) && (Meanbee.ShippingRules = {});

    Meanbee.ShippingRules.Condition.Customer = class extends Meanbee.ShippingRules.Condition
    {
        constructor(index, parent) {
            super(index, parent);
        }

        static getCategory(context) { // eslint-disable-line no-unused-vars
            return 'Customer Conditions';
        }

        static getVariables(context) {
            let variables = {};
            if (!context) {
                variables['customer_group'] = { label: 'Customer Group', type: ['number'] };
            }
            return variables;
        }
    }

    Meanbee.ShippingRules.Register.condition.add('Customer', Meanbee.ShippingRules.Condition.Customer);
})(window);