'use strict';
(function (global) {
    !('Meanbee' in global) && (Meanbee = {});
    !('ShippingRules' in Meanbee) && (Meanbee.ShippingRules = {});

    Meanbee.ShippingRules.Base = class {
        constructor(index, parent, container) {
            this.index = index;
            this.parent = parent;
            this.container = container;
        }

        set index(param) {
            this._index = param;
            return this;
        }

        get index() {
            return this._index;
        }

        get id() {
            if (this.parent instanceof Meanbee.ShippingRules.Base) {
                return `${this.parent.id}.${this.index}`;
            } else {
                return this.index;
            }
        }

        get root() {
            let root = this;
            while (root.parent instanceof Meanbee.ShippingRules.Base) {
                root = root.parent;
            }
            return root;
        }

        set context(context) {
            this._context = context;
            return this;
        }

        get context() {
            return this._context || this.parent && this.parent.context;
        }

        rerender() {
            let focussedElementId = global.document.activeElement.id;
            this.container.innerHTML = '';
            this.container.appendChild(this.render());
            Meanbee.ShippingRules.util.resizeFields();
            this.focus(focussedElementId);
        }

        focus(id) {
            let element = global.document.getElementById(id);
            if (element) {
                element.focus();
            }
        }

        renderRemoveButton() {
            if (this.parent instanceof Meanbee.ShippingRules.Base) {
                return Meanbee.ShippingRules.util.removeButton(this, () => {
                    this.parent.removeChildByIndex(this.index);
                    this.root.rerender();
                    this.focus(this.id);
                });
            }
            return [];
        }

        keyHandler(event) {
            let i;
            let caught = false;
            if (~['INPUT', 'SELECT', 'BUTTON', 'TEXTAREA'].indexOf(event.target.tagName)) {
                caught = true;
                if (event.keyCode === 27) { // Escape
                    event.preventDefault();
                    event.target.closest('li').focus();
                }
            } else {
                switch  (event.keyCode) {
                case 13: // Enter
                    if (event.target.tagName === 'LI') {
                        caught = true;
                        event.target.querySelector('input, select, button, textarea').focus();
                    }
                    break;
                case 37: // Left Arrow
                    if (event.target.tagName === 'LI') {
                        caught = true;
                        if (event.target.parentElement.parentElement.tagName === 'LI') {
                            event.target.parentElement.parentElement.focus();
                        }
                    }
                    break;
                case 38: // Up Arrow
                    caught = true;
                    if (event.target.tagName === 'LI') {
                        event.preventDefault();
                        let treeItems = Array.from(this.root.container.querySelectorAll('li'));
                        i = treeItems.indexOf(event.target);
                        if (treeItems[i - 1]) {
                            treeItems[i - 1].focus();
                        }
                    }
                    break;
                case 39: // Right Arrow
                    caught = true;
                    if (event.target.tagName === 'LI') {
                        event.preventDefault();
                        if (~(i = Array.from(event.target.children).map(child => child.tagName).indexOf('UL'))) {
                            event.target.children[i].children[0].focus();
                        }
                    }
                    break;
                case 40: // Down Arrow
                    caught = true;
                    if (event.target.tagName === 'LI') {
                        let treeItems = Array.from(this.root.container.querySelectorAll('li'));
                        i = treeItems.indexOf(event.target);
                        if (treeItems[i + 1]) {
                            treeItems[i + 1].focus();
                        }
                    }
                    break;
                default:
                }
                if (caught) {
                    event.preventDefault();
                }
            }
            if (caught)  {
                event.stopPropagation();
            }
        }
    }
})(window);